@import 'sizes';

.gl__page-content {
  padding-left: var(--page-content-padding-fl);
  padding-right: var(--page-content-padding-fl);
  max-width: 1020px;
  margin-left: auto;
  margin-right: auto;

  @include for-md-down {
    padding-left: var(--page-content-padding-md-down);
    padding-right: var(--page-content-padding-md-down);
  }

  @include for-sm-down {
    padding-left: var(--page-content-padding-small);
    padding-right: var(--page-content-padding-small);
  }

  @include for-xxs-down {
    padding-left: var(--page-content-padding-xxs-down);
    padding-right: var(--page-content-padding-xxs-down);
  }
}
